.container{
    /*display: none;*/
    
    background: white;
    padding: 2em 4em 2em 4em;
    text-align: center; 
    /*position: relative;*/ /* mit position: relative ist der pfeil innerhalb des containers | ohne ist er unten am Bildschirmrand*/
    border-radius: 20px;
    width: 340px;
    margin: auto;
    margin-top: 20em;
}


iframe{
    width: 100%;
    height: 929px;
}
.container label{
    color: var(--third-color);
    font-size: 34px;
    
}

.container label{
    color: var(--third-color);
    font-size: 34px;
    
}

.container input{
    border: 1.5px solid var(--third-color);
    border-radius: 10px;
    background: var(--third-color);
    padding: .4em;
    color: white;
    font-family: "Cooper Black";
    margin-top: .8em;
    margin-bottom: 1em;
    font-size: 24px;
}
.container input[type=text]:focus {
    outline: none;
}
.container input[type=number]:focus {
    outline: none;
}
.container input[type=password]:focus {
    outline: none;
}


img{
    background: white;
    margin-top: 1em;
    border: none;
    padding: 2px 15px;
    
    font-family: "cooper Black";
    border-radius: 10px;
    position: absolute;
    right: 1em;
    bottom: 1em;
    width: 4.5em;
    cursor: pointer;
}


h1{
    color: var(--third-color);
    font-size: 34px;
    text-align: center;
}


@media only screen and (max-width: 780px){
    .container{
        margin-top: 10em;
    }
}

.buttons-yes{
    text-align: center;
    margin: 0;
}

button{
    
    border: none;
    color: white;
    margin: .30em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-family: "Cooper Black";
    font-size: 30px;
    border-radius: 12px;
}

.buttonYes{
    background-color: var(--primary-color);
    padding: 10px 25px 13px ;
}

.buttonYes:hover{
    background-color: var(--primary-color2);
    color: white;
    cursor: pointer;
}
.buttonYes:focus{
    background-color: var(--primary-color3);
    color: var(--primary-color);
}

