textarea{
    background: var(--third-color);
    border: none;
    border-radius: 15px;
    padding: 1em 1em ;
    margin-top: 1em;
    color: white;
    font-family: "Cooper Black";
    font-size: 24px;
    width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
    resize: none;
}

textarea::-webkit-input-placeholder {
    color: rgb(255, 255, 255);
    
}

textarea:focus{
    outline: none;
}

input[type="file"]{
    display: none;
}

